var browserWindow = {};

export function moveAllSymbolsToExisitingLibrary(context) {
  var libraries = getAllLibraries();
  getLayoutSettings(
    context,
    context.document.documentData().localSymbols(),
    libraries
  );
}

export function moveSelectedSymbolsToExisitingLibrary(context) {
  var libraries = getAllLibraries();
  getLayoutSettings(context, context.selection, libraries);
}

function moveToLibrary(context, symbols, library) {
  if (symbols.count() > 0) {
    var foreignSymbolsInLibraryByName = getSymbolsInDocByName(
      library.document()
    );
    var localSymbolsByName = getSymbolsInDocByName(
      context.document.documentData()
    );
    getIdMap(
      context,
      localSymbolsByName,
      foreignSymbolsInLibraryByName,
      library
    );
    for (var i = 0; i < symbols.count(); i++) {
      if (validate(symbols[i])) {
        // var presentatge = i / symbols.count() * 100;
        // browserWindow.webContents.executeJavaScript(
        //   `changeLoading(${presentatge},${JSON.stringify(
        //     "Moving " + symbols[i].name()
        //   )})`
        // );
        // context.api().message("Moving [" + i + "/" + selection.count() + "] ")
        replaceInstance(
          context,
          symbols[i],
          foreignSymbolsInLibraryByName,
          library
        );
      }
    }
    // browserWindow.webContents.executeJavaScript(
    //   `changeLoading(${100},${JSON.stringify(
    //     movedSymbolsNumber + " Re-attached "
    //   )})`
    // );
    // context
    //   .api()
    //   .message(
    //     movedSymbolsNumber +
    //       " Moved Symbols  and " +
    //       movedInstancesNumber +
    //       " reattached Symbols instances "
    //   );
    browserWindow.webContents.executeJavaScript(
      `successLoading(${JSON.stringify(
        movedSymbolsNumber +
          " Moved Symbols  and " +
          movedInstancesNumber +
          " reattached Symbols instances "
      )})`
    );
  } else {
    context.api().message("No selection found!");
    browserWindow.webContents.executeJavaScript(`errorLoading()`);
  }
}

function getLayoutSettings(context, symbols, librariesArray) {
  browserWindow = new BrowserWindow(options);
  //browserWindow.loadURL("http://localhost:3000/");
  browserWindow.loadURL(require("../assets/UI/index.html"));
  LOG(librariesArray);

  browserWindow.once("ready-to-show", () => {
    browserWindow.show();
    browserWindow.webContents.executeJavaScript(
      `getLibraries(${JSON.stringify(librariesArray)})`
    );
  });
  browserWindow.webContents.on("setLibrary", function(libraryIndex) {
    try {
      moveToLibrary(context, symbols, librariesArray[libraryIndex].Reference);
    } catch (err) {
      browserWindow.webContents.executeJavaScript(`errorLoading()`);
    }
  });
  browserWindow.webContents.on("openURL", function(url) {
    NSWorkspace.sharedWorkspace().openURL(NSURL.URLWithString(url));
  });
}

function validate(symbol) {
  if (symbol.class() == MSSymbolMaster || symbol.class() == MSSymbolInstance) {
    return true;
  } else {
    context.api().message("layer selected is not Symbol ");
  }
  return false;
}

